@import "../../sassStyles/variables";
@import "../../sassStyles/mixins";

.contact {
  main {
    @include flexCenter(column);

    .contact-info {
      height: 70vh;
      width: 70%;
      max-width: 600px;
      @include flexCenter(column);
    
      h2 {
        color: $coral;
        margin: 0.5em 0;
      }
      
      p {
        text-align: center;
        font-size: 16px;
        margin: 0.5em 0;
      }
    }
  }
}